import { mapState, mapMutations, mapActions } from 'vuex'
import MoreOperate from '../moreOperateTempage'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      oItem: ''
    }
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapMutations({
    }),
    ...mapActions([
      'allThumbsUp',
      'allCollection'
    ]),
    // 播放暂停音频,跳转视频页面
    onSkipDetailsPageFn (item) {
      let url = ''
      const oJson = {
        keywords: item.keywords || '',
        vid: item.vid || item.id,
        authorId: this.authorId,
        page: item.page,
        type: this.type
      }
      if (item.is_hide || item.del) {
        this.showToastFn('该课程已下架,无法查看')
        return false
      }
      switch (item.v_type) {
        case 0:
          url = `/pages/subject/subjectMusic/musicDetails/index?oJson=${JSON.stringify(oJson)}`
          break
        case 1:
          if (item.vid) item['id'] = item.vid
          url = `/pages/subject/subjectVideo/videoDetails/index?oJson=${encodeURIComponent(JSON.stringify(item))}`
          break
      }
      uni.navigateTo({
        url
      })
    },
    // 音频点赞
    onLikeFn (item, status) {
      const data = {
        id: item.vid || item.id,
        likeType: this.type,
        that: this,
        type: 0,
        status
      }
      if (item.is_hide || item.del) {
        this.showToastFn('该课程已下架,无法查看')
        return false
      }
      if (this.userInfo) {
        this.allThumbsUp(data)
      } else {
        this.onSkipPageFn(`/pages/login/index`)
      }
    },
    // 音频收藏
    onCollectionFn (item, status) {
      const data = {
        collectionType: this.type,
        id: item.vid || item.id,
        that: this,
        type: 0,
        status
      }
      if (item.is_hide || item.del) {
        this.showToastFn('该课程已下架,无法查看')
        return false
      }
      if (this.userInfo) {
        this.allCollection(data)
      } else {
        this.onSkipPageFn(`/pages/login/index`)
      }
    },
    // 跳转到分享页
    onSkipThoughtFn (item) {
      if (item.is_hide || item.del) {
        this.showToastFn('该课程已下架,无法查看')
        return false
      }
      if (this.userInfo) {
        switch (item.v_type) {
          case 0:
            item['skipPath'] = 'musicDetails'
            break
          case 1:
            item['skipPath'] = 'videoDetails'
            item['special_id'] = item.vid || item.id
            break
        }
        this.oItem = item
        this.$refs.child.onUniPopupStateFn(true)
      } else {
        this.onSkipPageFn(`/pages/login/index`)
      }
    },
    // 跳转页面
    onSkipPageFn (url) {
      uni.navigateTo({
        url: url
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 2000
      })
    }
  },
  components: {
    MoreOperate
  },
  props: {
    lists: {
      type: Array,
      default: []
    },
    type: {
      type: String,
      default: ''
    },
    authorId: {
      type: String,
      default: ''
    }
  }
}
